<template>
<v-container>
        <v-layout wrap
                  justify-center
                  align-center>
            <v-flex    xs12
                       sm7
                       md6
                       lg6>
            <v-card class="mt-10">
                <v-form @submit.prevent="sendEvidence" class="pa-10 text-center">
                    <h3>ສົ່ງຫຼັກຖານຢືນຢັນການຊໍາລະເງິນ</h3>
                    <div class="file-input">
                        <label class="v-btn info" for="file">
                            <i class="fas fa-upload fa-lg mr-2"></i>
                            ອັບໂຫຼດຫຼັກຖານ</label>
                        <input @change="imgUpload" type="file" id="file">
                    </div>
                    <v-text-field v-model="orderId" label="orderID" outlined type="text"></v-text-field>
                    <v-textarea v-model="description" label="ລາຍລະອຽດເພີ່ມເຕີມ" outlined></v-textarea>
                    <v-btn type="submit" class="primary">ສົ່ງຫຼັກຖານຢືນຢັນການຊໍາລະເງິນ</v-btn>
                    <br> <br>
                    <small>* ເມື່ອທ່ານໂອນເງິນຜ່ານລະບົບແລ້ວໃຫ້ສົ່ງຫຼັກຖານຢືນຢັນການໂອນເງິນ</small>
                </v-form>
            </v-card>
            </v-flex>
        </v-layout>
</v-container>
</template>

<script>
    export default {
        data: () => ({
            orderId: '',
            description: '',
            file: '',
            base64: ''
        }),
        methods: {
            imgUpload(e) {
                const file = e.target.files[0]
                const reader = new FileReader() // Only read base64 not FormData
                if(file['size'] < 2000000) {
                    reader.onloadend = e => {
                        this.base64 = e.target.result
                    }
                    reader.readAsDataURL(file) // Base64 Reader
                    this.file = file // FormData Image
                }else {
                    this.$swal(
                        'Oops...',
                        'You are upload a large file',
                        'error'
                    )
                }
            },
           async sendEvidence () {
                const formData = new FormData()
                formData.append('orderId', this.orderId)
                formData.append('image', this.file)
                formData.append('description', this.description)
                 await this.$axios.post('evidence/post', formData)
               alert('suc')
            },
        },
        created () {
            this.orderId = this.$route.params.id
        }
    }
</script>

<style lang="scss" scoped>
    .file-input {
        width: 200px;
        margin: auto;
        height: 68px;
        position: relative;
        label, input {
            background: #fff;
            color: #2196F3;
            width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            padding: 10px;
            border-radius: 4px;
            margin-top: 7px;
            cursor: pointer;
        }
        input {
            opacity: 0;
            z-index: -12;
        }
    }
</style>
